nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  overflow: hidden;
  padding-top: 12px;
  padding-bottom: 8px;
}

.first-part {
  flex: 73;
  display: flex;
  flex-direction: column;
  /* justify-content: space-evenly; */
  align-items: left;
  padding: 8px;
}

.nav-item {
  box-sizing: border-box;
  padding: 16px;
  display: flex;
  justify-content: flex-start;
}

.nav-item:hover {
  background-color: #e8e8e3;
  cursor: pointer;
}

.second-part {
  flex: 27; 
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: left;
  padding: 8px;
}

.home {
  display: flex;
  justify-content: center;
}

.home-icon {
  flex: 0 0 auto;
  margin-right: 3px;
}
